import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ToastController } from 'ionic-angular';

import { ProfilePage } from '../profile/profile';
import { Localstorage } from '../../providers/localstorage';
import { Http, Headers, RequestOptions } from '@angular/http';

import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';


@IonicPage()
@Component({
  selector: 'page-works',
  templateUrl: 'works.html',
})
export class WorksPage {

  produtos = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, 
      public toastCtrl: ToastController,
      private http: Http,
      public localstorage:Localstorage) {

    this.localstorage = localstorage;
    this.getProdutos();

  }

  goToProfile(profileID):void
  {
    this.localstorage.setProfileID(profileID);
    this.navCtrl.push(ProfilePage);
  }

  ionViewDidLoad() {
    
  }

  getProdutos() {

      this.localstorage.getCategoria().then((categoria) => {

      var headers = new Headers();
      headers.append("Accept", 'application/json');
      headers.append('Content-Type', 'application/json' );
  
      let options = new RequestOptions({ headers: headers });

      let data = JSON.stringify({
        category: categoria
      });
      
      new Promise((resolve, reject) => {
        this.http.post('https://imagine-art.herokuapp.com/product/getAllProductsByCategory/',data, options)
        .toPromise()
        .then((response) =>
        {

          console.log(response.json().data);
          this.produtos = response.json().data;
          resolve(response.json());
          
        })
        .catch((error) =>
        {
          console.error('API Error : ', error.status);
          console.error('API Error : ', JSON.stringify(error));
          reject(error.json());
        });
      });


  })
  .catch((err) => {
      console.log("Error occurred :", err);
  });

  }

}



